.spw-spinner {
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: relative;
  width: 100%;
}
.spw-spinner .spw-outer-wheel {
  width: 100%;
}
.spw-spinner .spw-inner-wheel {
  position: absolute;
  width: 80%;
}
.spw-spinner .spw-static-x {
  position: absolute;
  width: 75%;
}

